.hero {
  background-image: url("../../../../assets/images/discolines.png");

  background-size: cover;
  background-position: center;

  height: calc(100vh - 4rem);

  position: relative;

  overflow: hidden;
  padding: 2rem;
}

.hero {
  /* height: 100%; */
  max-height: 100%;
}

.hero .fit-height {
  height: fit-content;
}

.features-explained {
  background-image: url("../../../../assets/images/Features-BG.png");

  background-size: cover;
  background-position: center center;
}

.future-talk {
  background-image: linear-gradient(
    267.64deg,
    rgba(84, 88, 247, 0.6) -3.51%,
    rgba(255, 0, 245, 0.42) 25.51%,
    rgba(15, 239, 253, 0.402) 117.97%
  );
  background-size: cover;
  text-align: center;
  min-height: unset;
  padding: 2rem;
}

.categories {
  background-image: url("../../../../assets/images/Categories-BG.png");
  background-size: cover;
  background-position: center;
  height: 100vh;

  text-align: center;
}

.categories .card {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  z-index: 10 !important;
  top: 1rem;
  box-shadow: -10px -10px rgba(242, 242, 242, 0.529);
}

@media (max-width: 600px) {
  .categories {
    height: max-content;
  }
}

.nest-platform {
  background-color: #1d1e20;
}
