header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #f2f2f2;
  height: fit-content;
}

.logo {
  height: 3rem;
  display: flex;

  align-items: center;
  gap: 10px;
  white-space: nowrap;
}

.logo > img {
  object-fit: contain;
  height: 100%;
}

.logo span {
  font-weight: bold;
}

nav {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 20px;
}

nav a {
  text-decoration: none;
  color: inherit;
}

nav > button {
  margin-right: 0;
}
